body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.clients-list-wrapper {
    width: 60%;
    display: flex;
    height: 330px;
    position: absolute;
    top: 0px;
}
.clients-list-wrapper ul {
    overflow-y: scroll;
    height: 100%;
    
}
.clients-list-wrapper ul li{
    padding: 10px 45px 10px 25px;
    border-radius: 5px;
    color: #007AFF;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
}
.clients-list-wrapper ul li:hover{
   background-color: #007AFF;
   color: white;
}

.clients-list-wrapper .list-wrapper{
    width: 33%;
}
button {
    padding: 25px 80px;
    border-radius: 25px;
    text-decoration: none;
    color: #FFFFFF;
    background-color: #4eb5f1;
    text-align: center;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    border: none;
    box-shadow: 0px 2px 2px 0px grey;
    font-size: 25px;
}
.header-wrapper {
    height: 37px;
    width: 100%;
    border-bottom: 1px solid gray;
}
.map-wrapper {
    margin-left: 10px;
}
.modal {
  position: fixed;
  width: 1130px;
  height: 375px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 0px 2px 10px 2px rgba(0, 0, 0, 0.3);
  background: white;
  border-radius: 10px;
  z-index: 500; 
  overflow: hidden;
}


.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 100; 
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
h3 {
    margin: 0;
    padding: 10px
}
